import React, { Component } from 'react';
import abcjs from "abcjs/midi";
import 'abcjs/abcjs-midi.css';
import request from 'request';
const $ = require('cheerio');

class App extends Component {
  constructor(){
    super();
    this.randomTune = null;
  }

  state = {
    scale: 2,
    note: 0,
    delay: 4000,
    tuneBookString: "DEFGABcd",
  }

  requestRandomTune = () => {
    request( 'http://www.norbeck.nu/abc/random.asp',
      ( error, require, body )=>{

        if( error ) throw error;

        this.randomTune = $('div[id="abc"]',body).text()
        console.log(this.randomTune);
      })
  }

  renderNotes = () => {

    let idOfRenderedTag = 'abc';
    let tuneBookString = this.state.tuneBookString;
    let params = {};

    this.tuneObject = abcjs.renderAbc( idOfRenderedTag, tuneBookString, params );

  }

  componentDidMount = () => {
    this.renderNotes();
    this.requestRandomTune()
  }

  renderRandomTune = () => {
    this.setState({tuneBookString: this.randomTune})
    this.componentDidMount();
  }

  render() {

    return (
      <div className = "App">
        <div className = "header">
          <div id = "midi"></div>
          <button onClick = {this.renderRandomTune}>next</button>
        </div>
        <div id='abc'></div>
        <div id='engine'></div>
      </div>
    );
  }
}

export default App;
